import React from 'react';
import {Link} from 'react-router-dom';

import {WingBlank} from 'antd-mobile';

function GameIntroduce() {
    return (<WingBlank size="md">
        <article>
            <h3></h3>
            <p> 0~9  4 </p>
            <p>*A 1A  2A *B  1B  2B </p>
            <p><br/>
                 4  1 3 5 7 1 2 3 4  1  1  1  A2 3   B4  1 2 3 4  1A 1B<br/>
                 2 4 6 8  0A 0B<br/>
                 1 3 5 8 1 3 5  8  3A 0B<br/>
                 7 5 3 1 7 5 3 1  0A 4B
            </p>
            <p> 4A  10  4A</p>
            <p></p>
        </article>

        <Link className="am-button am-button-primary" to={'/game'}></Link>

        <div style={{
            // textAlign: 'right',
            padding: '15px 0',
        }}><Link to={'/'}> &lt;&lt; </Link></div>

    </WingBlank>);
}

export default GameIntroduce;



